$base-color: $secondaryGreen , $secondaryRed , $secondaryBlue;

.main-body-container{
    width: calc(100% - 270px);
    position: absolute;
    right: 0;
    .top-bar{
        z-index: 9;
        position: fixed;
        width: calc(100% - 270px);
        top: 0;
        background:$offBlack;
        border-left: solid 2px $black;
        padding:20px 40px;
        .top-bar-inner-cont{
            display:flex;
            justify-content: space-between;
            width: 100%;
            height: 40px;

            form , ul{
                display: flex;
            }
    
            .search-box-cont{
                width:40%;
                max-width:350px;
                min-width: 100px;
                position:relative;
                height: 100%;
                
                .search-box{
                    position: absolute;
                    background: $highlightBlack;
                    border:none;
                    color: $white;
                    top:0;
                    left: 0;
                    width:100%;
                    height: 100%;
                    border-radius:9px;
                    padding-left: 40px;
                }

                .search-box:focus{
                    border: $primaryBlue 2px solid;
                }
                
                .search-box:focus-visible{
                    border:none;
                }

                input::placeholder{
                    color: $white;
                }
    
    
                .search-btn{
                    position: absolute;
                    left: 10px;
                    top:0;
                    bottom:0;
                    background: transparent;
                    border:none;
                    height: fit-content;
                    margin:auto 0;

                    svg{
                        color: $white;
                        width: 20px;
                        height: 20px;
                    }
                }
    
                .shortcut{
                    position:absolute;
                    right: 0;
                    top:0;
                    bottom:0;
                    margin:auto 0;
                    height: 70%;
                    width: 40px;
                    border:none;
                    border-radius:5px;
                    color:$white;
                    background: $black;
                    margin-right: 10px;
                }
            }
    
            .top-bar-side-nav{
                width:30%;
                min-width: 260px;
                max-width: 300px;
                justify-content: space-between;
                align-items: center;
                li{
                    margin-right:10px;
                    
                    a{


                        .icon{
                            position: relative;
                            width: 20px;
                            height: 20px;
                        
                            svg{
                                width: 100%;
                                height: 100%;
                            }

                            .notifications{
                                position: absolute;
                                width:8px;
                                height: 8px;
                                background: red;
                                top: -3px;
                                right:-3px;
                                border-radius:5px;
                            }
                        }

                        .notifications-cont{
                            position: relative;
                            
                            .triangle{
                                width: 0;
                                height: 0;
                                border-left: 10px solid transparent;
                                border-right: 10px solid transparent;
                                border-bottom: 20px solid $black;
                                position: absolute;
                                top: -20px;
                                left: 180px;
                            }

                            .notification-dropdown{
                                position: absolute;
                                display: none;
                                left: -180px;
                                top: 60px;
                                background: $black;
                                padding: 23px 20px;
                                width: 280px;
                                border-radius: 9px;
                                border: $highlightBlack 0.1px solid;

                                .comment-block{
                                    padding:10px 0px;
                                    
                                }

                                // .comment-block:not(:last-child){
                                //     border-bottom: 1px $highlightBlack solid;
                                // }

                                button{
                                    width: 100%;
                                }
                            }
                        }


                        .avatar{
                            height: 50px;
                            width: 50px;
                            border-radius: 100%;
                            background-image:url('https://images.unsplash.com/photo-1494790108377-be9c29b29330?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
                            background-size: cover;
                            background-position:center;
                        }
                    }
                }

                .create-btn{
                    height: 100%;
                    
                    button{
                        background: $primaryBlue;
                        height: 100%;
                        border: none;
                        padding: 0 16px;
                        display: flex;
                        align-items: center;
                        width: 110px;
                        justify-content: space-between;
                        color:$white;
                        border-radius: 9px;
                        font-weight: 600;

                        div:first-child{
                            display: flex;
                            align-items: center;
    

                            svg{
                                width: 20px;
                                height: 20px;
                            }
                        }
                    }
                }
            }
        }
    }

    .main-content{

        padding-top: 80px;

        .heading-with-bar{
            height: 60px;
            display: flex;
            justify-content: space-between;

            .filter-dropdown{
                position: relative;
                width: fit-content;
                color:$white;
                
                .dropdown-container{

                    >li{
                        display: flex;
                        justify-content: space-between;

                        >div{
                            margin-left: 10px;
                        }
                    }

                    .dropdown{
                        display: none;
                    }

                    .clicked{
                        display: block;
                        position: absolute;
                        bottom: -260%;
                        // width: 100%;
                        left: -20px;
                        width: 150px;
                        background: $offBlack;
                        z-index: 2;
                         
                        li{
                            font-size: 0.8rem;
                            padding: 7px 12px;
                            border-radius: 9px;
                            transition: background 0.2s ease-out;
                        }

                        li:hover{
                            background: $highlightBlack;
                        }
                    }
                }
            }

            .tag{
                color:white;
                display: flex;
                align-items: center;

                .color-indicator{
                    width: 20px;
                    border-radius: 3px;
                    margin-right: 8px;
                    height: 100%;
                    background: pink;
                }
            }

        }

        .main-content-container{
            padding: 20px 40px;

            .heading{
                padding: 30px 0;
            }

            .main-content-body{
                display: flex;
                flex-wrap: wrap;
                flex-shrink: 1;
                width: 100%;



                // left board / right board
                >div{
                    flex-grow: 1;
                    // min-width: 400px;
                    // width:calc((100% / 2) - 20px);
                    display: flex;
                    height: fit-content;

                    .dashboard{
                        color:$white;
                        width: 100%;
                        >div{
                            border-radius: 9px;
                            margin-bottom: 10px;
                        }

                        .figures , .send-message{
                            display: flex;
                            justify-content: space-between;
                        }

                        .figures{
                            background: $black;
                            border-radius: 9px;
                            padding: 5px;
                            flex-wrap: wrap;
                            flex-shrink: 1;


                            .figure{
                                width:49%;
                                min-width: 200px;
                                flex-grow: 1;
                                padding: 10px;
                                display: flex;
                                justify-content: space-between;
                                border-radius:9px;
                                
                                .customers , .income{
                                    display: flex;
                                    // align-items: baseline;
                                    .icon{
                                        width: 50px;
                                        height: 50px;
                                        background: pink;
                                        align-self: center;
                                        border-radius: 100px;
                                        margin-right: 10px;
                                    }



                                    span{
                                        font-size: 0.6em; 
                                    }
                                }
                            }
                        }

                        .welcome-customers{
                            >div{
                                margin:40px 0px;
                            }

                            .send-message{
                                align-items: center;
    
                                >div:first-child{
                                    width: 200px;
                                }
                            }

                            .customers-list{
                                display: flex;
                                justify-content: space-around;
                                flex-wrap: wrap;
                                .individual-customer{
                                    display: flex;
                                    flex-direction: column;
                                    align-items: center;
                                    .customer-image{
                                        position: relative;
                                        width:60px;
                                        height:60px;
                                        background: #272b30;
                                        background-size: cover;
                                        background-position: center;
                                        border-radius: 100%;
                                        margin-bottom: 5px;
                                    }

                                    .next::after{
                                        position: absolute;
                                        border-radius: 100%;
                                        height: 15px;
                                        width: 8px;
                                        content:"\f054";
                                        left:0;
                                        top:0;
                                        right: 0;
                                        bottom:0;
                                        margin:auto;
                                        font-weight: 900;
                                        font-family: "Font Awesome 5 Free";
                                    }
                                }
                            }
                        }
                    }
                }


                .main-content-body-left{
                    width: calc(50% - 10px);
                    margin-right: 10px;
                    min-width: 300px;
                }

                .main-content-body-sidebar{
                    min-width: 300px;
                    width: 20%;
                    .products-list-container{
                        .list-headings{
                            color: $gray;
                            border-bottom: 1px solid $highlightBlack;
                            padding-bottom:10px;
                            font-weight: 600;
                            display: flex;
                            justify-content: space-between;
                        }

                        .products-list{
                            margin-top: 20px;
                            .product{
                                display: flex;
                                justify-content: space-between;
                                height: 70px;
                                margin-bottom: 10px;

                                .image-container{
                                    display: flex;
                                    .image{
                                        border-radius: 9px;
                                        height: 100%;
                                        width: 70px;
                                        // background-image: url('https://images.unsplash.com/photo-1638913976954-8f7b612867c2?ixlib=rb-1.2.1&ixid=MnwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80');
                                        background-size: cover;
                                        // background: pink;
                                    }

                                    .product-name{
                                        padding:10px 0px 0px 10px;
                                    }
                                }

                                .product-price{
                                    text-align: right;
                                    
                                    h5{
                                        margin-bottom:5px;
                                    }
                                }

                            }

                            @for $i from 1 through 3 {
                                .product:nth-child(#{length($base-color)}n+#{$i}){
                                    .image{
                                        background: nth($base-color , $i);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.refunds-container{
    display: flex;
    padding-bottom: 20px;

    .icon{
        width: 50px;
        height: 50px;
        background: $secondaryRed;
        border-radius:100%;
        margin-right: 10px;
        position: relative
    }

    .icon::after{
        position: absolute;
        content: '\f291';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $primaryRed;
        height: 19px;
        width: 17px;
        top:0;
        left:0;
        right: 0;
        bottom: 0;
        margin: auto;
    }

    .message{
        width:calc(100% - 50px);
    }
}



.line::after{
    position: absolute;
    content: "";
    width: 50%;
    height: calc(100% - 30px);
    background: transparent;
    left: 30px;
    bottom: 0;
    top: 0;
    border-top: solid 1px transparent;
    border-left:$lightGray solid 2px ;
    border-bottom: $lightGray solid 2px;
    border-right:solid 1px transparent;
    border-radius: 10px;
    z-index: 1;
}



// filter checkboxes

.filter-notif{
    padding-bottom: 10px;
    .check{
        input[type="checkbox"] , input[type="checkbox"]:checked{
            border-radius:2px
        }
    }
}

.secondary-filter{
    border-top: 2px solid $highlightBlack;
    padding-top: 30px;
    margin-top: 30px;
    .check{
        input[type="checkbox"] , input[type="checkbox"]:checked{
            border-radius:100%;
        }
    }
}

.buttons{
    display: flex;
    >button{
        width: 50%;
        margin-right: 10px;
    }
}


.tips-container{
    p{
        margin-bottom: 20px;
    }
    .tips{
        display:flex;
        flex-wrap:wrap;
        flex-shrink: 1;

        .tip{
            height: 80px;
            min-width: 250px;
            width: 50%;
            display:flex;
            flex-grow: 1;
            margin-bottom: 20px;
            padding-bottom: 20px;
            align-items: center;

            .image{
                position: relative;
                width: 80px;
                height: 80px;
                background: $lightGray;
            }

            .image::after{
                position: absolute;
                color:$gray;
                content:'\f86d';
                width: 20px;
                height: 20px;
                left:0;
                top:0;
                right: 0;
                bottom:0;
                margin:auto;
                font-weight: 900;
                font-family: "Font Awesome 5 Free";
            }

            .tip-link{
                display: flex;
                flex-direction: column;

                .more-info , .time{
                    display: flex;
                }

                .more-info{
                    padding-top:10px;

                    .tag{
                        background: $primaryGreen;
                        padding:2px 5px;
                        border-radius: 5px;
                    }
                }

                .time{
                    align-items: center;
                    margin-left: 10px;
                    position: relative;
                    .avatar{
                        background-image: url('https://images.unsplash.com/photo-1497215728101-856f4ea42174?ixlib=rb-1.2.1&ixid=MnwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80');
                        position: absolute;
                        background-size: cover;
                        width: 28px;
                        height: 28px;
                        border-radius: 100%;
                        z-index: 1;
                    }

                    .description{
                        position: absolute;
                        border-radius: 0px 9px 9px 0px;
                        font-size: 14px;
                        min-width: 90px;
                        left: 18px;
                        padding-left: 10px;
                        border: solid $highlightBlack 2px;
                    }
                }
            }
        }
    }
}


.get-customers-container{
    .social-buttons{
        display: flex;
        margin-top:20px;

        button{
            width: calc(100% / 3);       
        }

        button:nth-child(even){
            margin:0px 10px;
        }
    }
}



.graph-container{
    width: 90%;
    margin: auto;
    padding: 30px 0px 30px 8px;

    .chart{
        position: relative;
    
        ul{
            list-style-type: none;
            margin:0;
            padding: 0;
        }
        
        .numbers {
            li{
                position: absolute;
            }
        }
        
        .numbers-horizontal{
            align-items: flex-end;
            position: absolute;
            display: flex;
            bottom: -30px;
            justify-content: space-between;
            z-index: 1;
            width: 100%;
    
            li{
                text-align: center;
                width:calc(100% / 7);
                // width: calc(100% - 20px);
            }
    
            li:nth-child(even){
                margin: 0px 10px;
            }
        }
    
        
        .bars{
            position: absolute;
            display: flex;
            align-items: flex-end;
            height: 100%;
            bottom: 0;
            width: 100%;
            z-index: 2;
    
            li{
                width:calc(100% / 7);
            }
    
            li:nth-child(even){
                margin: 0px 10px;
            }
    
            .bar{
                position: relative;
                height: 50%;
                width:100%;
                bottom: 0px;
                display: flex;
                align-items: flex-end;
    
                .views-popup{
                    display: none;
                    position:absolute;
                    top: -30px;
                    left: -60px;
                    flex-direction: column;
                    padding:10px;
                    border-radius: 9px;
                    background: #272b30 !important;
    
                    .date{
                        white-space: nowrap;
                    }
    
                    .views{
                        display: flex;
                        align-items: center;
                        >div{
                            width:10px;
                            height: 10px;
                            margin-right: 5px;
                            border-radius: 2px;
                        }
    
                    }
    
                }
    
                .bar-increase{
                    width: 100%;
                    height: 100%;
                    border-radius: 9px;
                    transition: background 0.2s ease-out;
                }
    
            }
    
            @for $i from 1 through 3 {
                .bar:nth-child(#{length($base-color)}n+#{$i}){
                    .views{
                        >div{
                            background: nth($base-color , $i);
                        }
                    }
                }
            }
    
            .bar:hover .bar-increase{
                background: $primaryBlue !important;
            }
    
            .bar:hover .views-popup{
                display: block;
            }
    
            @for $i from 1 through 3 {
                .bar:nth-child(#{length($base-color)}n+#{$i}){
                    >div{
                        background: nth($base-color , $i);
                    }
                }
            }
        }
    
    }
}


.line-chart{
  list-style: none;
  margin: 0;
  padding: 0;
}


.point{
  width:10px;
  height:10px;
  border:2px solid $primaryBlue;
  border-radius:50%;
  position: absolute;
  background: $offBlack;
  z-index:1;
}


.hypline{
  position: absolute;
  height:3px;
  transform-origin: left bottom;
  background:$primaryBlue;
}

.numbers{
  list-style: none;
  margin: 0;
  padding: 0;
}

.numbers li{
  position: absolute;
  width: 100%;
  border-bottom: 2px solid $highlightBlack;
}

.hamburger-icon{
    width: 40px;
    height: 24px;
    align-self: center;
    >div{
        height: calc((100% - 15px) / 3);
        width: 100%;
        background: $white;
        margin-bottom: 5px;
    }
}

.mobile-list{
    display: none;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    background: $offBlack;
    height: 100%;
    overflow: hidden;
    z-index: 999;
}

.block{
    display: block;
}