@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&family=Ubuntu:wght@300;400&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css');
@import './colors.scss';

*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    text-decoration: none;
    list-style-type:none; 
}

body{
    background: $black;
}

h1,h2,h3,h4,h5,h6,a{
    color:$white;
    font-family: 'Ubuntu' , sans-serif;
}

h3{
    font-size: 2.5em;
}


h4{
    font-size: 1.3em;
}

a{
    font-size: 0.8em;
}


p,li{
    font-family: 'Open Sans' , sans-serif;

}

p{
    font-size: 0.8em;
}

.section{
    background: $offBlack;
    padding: 20px;

    .heading-with-bar{
        padding-bottom: 20px;
    }
}


.btn{
    background: transparent;
    color: $white;
    padding: 11px 14px;
    border: $highlightBlack 2px solid;
    border-radius: 9px;
    font-size: 0.8em;
    transition: border 0.5s ease-out;
}

.full-width{
    width: 100%;
}

.btn:hover:not(.fill){
    border: $gray 2px solid;
}

.fill{
    background: $primaryBlue;
    color:$white;
}

.outline{
    border: $white 1px solid;
}

.disabled{
    opacity: 0.5;
}

.green , .red{
    font-size: 0.8em;
    padding: 2px 8px;
    border-radius:5px;
}

.green{
    background:$secondaryGreen;
    color:$primaryGreen;
    font-weight: 600;
}

.red{
    background: lighten($secondaryRed,10%);
    color:$primaryRed;
    font-weight: 600;
}

.like{
    background: $primaryRed;
}

.chat{
    background: $primaryBlue;
}

.blue{
   color:$primaryBlue;
}

.light{
    opacity: 0.5
    
}

.bar-increase{
    animation: increase 0.9s forwards ease-in;
}

@keyframes increase{
    0%{
        height: 0%
    }

    100%{
        height: 100%;
    }
}

.secondary{
    label{
        opacity: 0.5;
    }
}

.selection{
    display: flex;
    justify-content: space-between;
    
    label{
        font-weight: 600;
    }
    
    .check{
        margin-bottom:10px;
        input[type="checkbox"]{
            position: relative;
            appearance:none;
            height: 20px;
            width: 20px;
            background:transparent;
            border: 1px $highlightBlack solid;
        }

        input[type="checkbox"]::after{
            content:"\f00c";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            font-size: 10px;
            width: 10px;
            height: 10px;
            position: absolute;
            top:0;
            left:0;
            right: 0;
            bottom: 0;
            margin: auto;
            display: none;
        }

        input[type="checkbox"]:checked{
            background:$primaryBlue
        }

        input[type="checkbox"]:checked::after{
            display: block;
        }
    }    
}

@keyframes anidown{
    0%{
        transform: translateY(-100vh);
    }

    95%{
        transform: translateY(4vh)
    }

    100%{
        transform: translateY(0vh)
    }
}

.clicked{
    animation: popout forwards 0.6s ease-in-out;
    display: block !important;
}

@keyframes popout{
    0%{
        transform:scale(0);
    }

    80%{
        transform:scale(1.2);
    }

    100%{
        transform:scale(1);
    }
}

.highlighted{
    position: relative;
    overflow: hidden;
    border-top: solid #474e53 1px;
    border-bottom: solid #1a1c1e 1px;
    box-shadow: 0px 0px 2px $black;

    >div{
        position: relative;
        z-index: 1;
    }
}

.highlighted::after{
    content: '';
    position: absolute;
    padding: 5px;
    top:0;
    left: 0;
    height: 100%;
    width: 100%;
    background: $highlightBlack;
    // animation: slide 0.9s forwards ease-out;
}

.rotate{
    transform:rotate(180deg);
    transition: all 0.5s ease-out;
}

.bak{
    transform:rotate(0deg);
    transition: all 0.5s ease-out;
}

@keyframes slide{
    0%{
        transform: translateX(-100%);
    }
    100%{
        transform: translateX(0%)
    }
}

.drp-nav{
    transition:all 0.5s ease-out;
    display: block;
    animation: drop 0.5s forwards ease-out;
}

@keyframes drop{
    0%{
        height:0%;
    }

    100%{
        opacity: 1;
        height: 100%;
    }
}

  


@import './templates/main.scss';
@import './templates/sidebar.scss';
@import './templates/login.scss';
@import './templates/animations.scss';
@import './templates/comment.scss';
@import './mobile.scss';